.wrapper {
  display: inline-flex;
  min-width: 100px;
  padding: 9px 16px;
  border-radius: 4px;
  font-weight: 700;
  font-size: 1.6rem;
  cursor: pointer;
  background-color: var(--white);
  border: 1px solid transparent;
  user-select: none;
  text-align: center;
  justify-content: center;

  + .wrapper {
    margin-left: 8px;
  }

  &.disable {
    //dua cao swrapper de duoc uu tien
    pointer-events: none;
    opacity: 0.5;
  }
}

.icon + .title,
.title + .icon {
  margin-left: 8px;
}
.icon {
  display: inline-block;
  width: 24px;
  text-align: center;
}

//button types
.primary {
  color: var(--white);
  background-color: var(--primary);
  border-color: var(--primary);

  &:hover {
    border-color: var(--primary);
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), #fe2c55;
  }
}

.outline {
  color: var(--primary);
  background-color: var(--primary);

  &:hover {
    border-color: var(--primary);
    background-color: rgba(254, 44, 85, 0.06);
  }
}

.rounded {
  border-radius: 999px;
  box-shadow: 0 2px 8px rgb(0 0 0 /6%);
  border-color: rgba(22, 24, 35, 0.12);

  &:hover {
    border-color: rgba(22, 24, 35, 0.2);
    background-color: rgba(22, 24, 35, 0.03);
  }
}

.text {
  &:hover {
    text-decoration: underline;
  }
}
//button size

.small {
  min-width: 88px;
  padding: 4px 16px;
}

.large {
  min-width: 140px;
  padding: 14px 16px;
}
